* {
  box-sizing: border-box;
}

img, p {
  margin: 0;
}

body {
  padding-bottom: 20px;
}

input {
  font: inherit;
  width: 300px;
  border: none;
  border-radius: 5px;
  padding: 8px;
}

button {
  cursor: pointer;
  color: #fff;
  background-color: #006400;
  border: none;
  border-radius: 5px;
  margin-left: 5px;
  padding: 8px 12px;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

.search-form {
  height: 45px;
  background-color: #427ef5;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  display: flex;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 20px;
  margin-bottom: 20px;
  padding-left: 15px;
  padding-right: 15px;
  display: flex;
}

.photo-card {
  width: calc(25% - 15px);
}

.gallery__item {
  width: 100%;
  height: 270px;
  display: flex;
}

div.info {
  text-align: center;
  flex: 25%;
  justify-content: space-between;
  padding: 20px;
  display: flex;
}

div.info p {
  margin-top: 5px;
  display: flex;
}

.load-more {
  min-width: 160px;
  border-radius: 8px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.load-more.hidden {
  display: none;
}

